import React from "react";
import { Link } from "react-router-dom";
import "./styles/Footer.css";

function scrollToTop() {
  window.scrollTo(0, 0);
}

function Footer(props) {
  return (
    <div className="footer-container">
      <div className="footer-overlay">
        <div className="links-container">
          <div className={props.active === "home" ? "footer-active footer-link" : "footer-link"}>
            <Link to="/" onClick={ scrollToTop() }>Home</Link>
          </div>
          <div className={props.active === "hsq" ? "footer-active footer-link" : "footer-link"}>
            <Link to="/hsq" onClick={ scrollToTop() }>Healthcare Specialists and Qualifications</Link>
          </div>
          <div className={props.active === "services" ? "footer-active footer-link" : "footer-link"}>
            <Link to="/services" onClick={ scrollToTop() }>Services</Link>
          </div>
          <div className={props.active === "customer-resources" ? "footer-active footer-link" : "footer-link"}>
            <Link to="/customer-resources" onClick={ scrollToTop() }>Customer Resources</Link>
          </div>
          <div className={props.active === "contact-us" ? "footer-active footer-link" : "footer-link"}>
            <Link to="/contact-us" onClick={ scrollToTop() }>Contact Us</Link>
          </div>
          <div className={props.active === "become-a-driver" ? "footer-active footer-link" : "footer-link"}>
            <Link to="/become-a-driver" onClick={ scrollToTop() }>Become a Driver</Link>
          </div>
        </div>
        <div className="copyright">
          <p>&copy; 2020 - HealthEx Courier</p>
        </div>
      </div>
    </div>
  );
}

export default Footer;
