import React, { Component } from "react";
import "../styles/Buttons.css";
import image from "../../images/window-images/services-2.jpg";
// import axios from "axios";

class MobileFeedback extends Component {
  state = {
    name: "",
    address: "",
    phone: "",
    email: "",
    comments: ""
  }

  // async submit(name, address, phone, email, comments) {
  //   await axios.post('./api/feedback-form', {
  //     name,
  //     address,
  //     phone,
  //     email,
  //     comments
  //   });
  // }

  submitForm(e) {
    e.preventDefault();
    const { name, address, phone, email, comments } = this.state;

    let popup;
    if (name !== "" && address !== "" && phone !== "" && email !== "" && comments !== "") {
      popup = document.querySelector('.submit-success-popup');
      // this.submit(name, address, phone, email, comments);
      this.setState({ 
        name: "",
        address: "",
        phone: "",
        email: "",
        comments: ""
      });
    } else {
      popup = document.querySelector('.submit-error-popup');
    }
    popup.classList.add('popup-visible');
    setTimeout(function() {
      popup.classList.remove('popup-visible')
    }, 2000);
  }

  handleChange = e => {
    this.setState({[e.target.name]: e.target.value});
  }

  render() {
    return (
      <div
        className="mobile-only"
      >
        {this.props.hero === true ? <div className="mobile-hero-scrolled"></div> : <div></div>}
            <div
              className="image-container"
              style={{ backgroundImage: `url("${image}")` }}
            ></div>
          <div className="page-container">
            <div className="page-title">
              <h2>Feedback and Suggestions</h2>
            </div>
            <div className="page-content">
              <div className="mobile-form-container">
                <p style={{textAlign: "center"}}>
                  <b>We want to know what you think!</b>
                </p>
                <br />
                <p>
                  &emsp;It is our goal to always improve on our services. If you have
                  any questions, comments, or suggestions regarding our services,
                  drivers, or staff, PLEASE let us know! We always want to cater
                  our services to your specific needs.
                </p>
                <form>
                  <input
                    name="name"
                    value={this.state.name}
                    onChange={this.handleChange}
                    id="name"
                    placeholder="Enter First Name *"
                    required
                  />
                  <br />
                  <input
                    name="address"
                    value={this.state.address}
                    onChange={this.handleChange}
                    id="address"
                    placeholder="Enter Street Address *"
                    required
                  />
                  <br />
                  <input
                    name="phone"
                    value={this.state.phone}
                    onChange={this.handleChange}
                    id="phone"
                    placeholder="Enter Phone Number *"
                    required
                  />
                  <br />
                  <input
                    name="email"
                    value={this.state.email}
                    onChange={this.handleChange}
                    id="email"
                    placeholder="Enter Email Address *"
                    required
                  />
                  <br />
                  <textarea
                    name="comments"
                    value={this.state.comments}
                    onChange={this.handleChange}
                    id="comments"
                    cols="30"
                    rows="10"
                    placeholder="Comments *"
                    required
                  ></textarea>
                  <div className="btn-container">
                    <button
                      className="default-btn dark-btn"
                      onClick={e => this.submitForm(e)}
                    >
                      Submit
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
    );
  }
}

export default MobileFeedback;