import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { DownArrow, UpArrow } from "../Svg.js";
import "../styles/Navbar.css";

class MobileNavbar extends Component {
    state = {
        isOpen: false
    }

    toggleNav() {
        let isOpen = this.state.isOpen;
        this.setState({ isOpen: !isOpen })
    }

    render() {
        return (
            <div className="position-mobile-nav">

            
            <div className="navbar-container mobile-nav">
                {this.state.isOpen ? 
                (
                    <div>
                        <div className="mobile-nav-button" onClick={() => this.toggleNav()}>
                            <div className="svg">
                                <UpArrow />
                            </div>
                        </div>
                        <div className="nav-items-container">
                            <Link to="/"><div className="nav-item" onClick={() => this.toggleNav()}><span>Home</span></div></Link>
                            
                            <Link to="/hsq"><div className="nav-item" onClick={() => this.toggleNav()}><span>Healthcare Specialists and Qualifications</span></div></Link>
            
                            <Link to="/services"><div className="nav-item" onClick={() => this.toggleNav()}><span>Services</span></div></Link>
                            
                            <Link to="/customer-resources"><div className="nav-item" onClick={() => this.toggleNav()}><span>Customer Resources</span></div></Link>
                            
                            <Link to="/contact-us"><div className="nav-item" onClick={() => this.toggleNav()}><span>Contact Us</span></div></Link>
                            
                            <Link to="/become-a-driver"><div className="nav-item" onClick={() => this.toggleNav()}><span>Become A Driver</span></div></Link>
                            
                            <a
                            rel="noopener noreferrer"
                            target="_blank"
                            href="http://healthexcourier.com/employee-resources-login.php"
                            >
                                <div className="nav-item" onClick={() => this.toggleNav()}>
                                    <span>Employee Login</span>
                                </div> 
                            </a>
                            
                        </div>
                    </div>
                ) : 
                <div className="mobile-nav-button" onClick={() => this.toggleNav()}>
                    <div className="svg">
                        <DownArrow />
                    </div>
                </div>
                }
            </div>
            </div>
        )
    }
}

export default MobileNavbar;
