import React from 'react'
import "../App.css";
import Hero from "../components/desktop/Hero";
import SmallPage from "../components/desktop/SmallPage";
import TextLeft from "../components/desktop/TextLeft";
import TextRight from "../components/desktop/TextRight";
import Divider from "../components/desktop/Divider";
import MobilePage from "../components/mobile/MobilePage";

export default function Hsq(props) {
    return (
        <div>
            {/* Mobile Components */}
            <MobilePage 
              hero={true}
              image="hsq-hero"
              title={props.items.pages[0].title}
              content={props.items.pages[0].content}
            />
            <MobilePage 
              image="hsq-1"
              title={props.items.textLefts[0].title}
              content={props.items.textLefts[0].content}
            />
            <MobilePage
              title={props.items.textRights[0].title}
              content={props.items.textRights[0].content}
            />
            <MobilePage 
              title={props.items.pages[1].title}
              content={props.items.pages[1].content}
            />
            <MobilePage 
              image="hsq-2"
              title={props.items.textLefts[1].title}
              content={props.items.textLefts[1].content}
            />
            <MobilePage 
              title={props.items.textRights[1].title}
              content={props.items.textRights[1].content}
            />
            <MobilePage
              title={props.items.pages[2].title}
              content={props.items.pages[2].content}
            />
            <MobilePage
              title={props.items.pages[3].title}
              content={props.items.pages[3].content}
            />
            <MobilePage
              image="hsq-3"
              content={props.items.textLefts[2].content}
            />
            <MobilePage
              content={props.items.textRights[2].content}
            />
            <MobilePage
              title={props.items.pages[4].title}
              content={props.items.pages[4].content}
            />

            {/* Desktop Components */}

            <Hero
              title={props.items.hero.title}
              subtitle={props.items.hero.subtitle}
              image={props.items.switch}
            />
            <SmallPage
              title={props.items.pages[0].title}
              content={props.items.pages[0].content}
            />
            <TextLeft
              image="hsq-1"
              title={props.items.textLefts[0].title}
              content={props.items.textLefts[0].content}
            />
            <TextRight
              image="hsq-1"
              title={props.items.textRights[0].title}
              content={props.items.textRights[0].content}
            />
            <SmallPage
              title={props.items.pages[1].title}
              content={props.items.pages[1].content}
            />
            <TextLeft
              image="hsq-2"
              title={props.items.textLefts[1].title}
              content={props.items.textLefts[1].content}
            />
            <TextRight
              image="hsq-2"
              title={props.items.textRights[1].title}
              content={props.items.textRights[1].content}
            />
            <SmallPage
              title={props.items.pages[2].title}
              content={props.items.pages[2].content}
            />
            <Divider />
            <SmallPage
              title={props.items.pages[3].title}
              content={props.items.pages[3].content}
            />
            <TextLeft
              image="hsq-3"
              content={props.items.textLefts[2].content}
            />
            <TextRight
              image="hsq-3"
              content={props.items.textRights[2].content}
            />
            <SmallPage
              title={props.items.pages[4].title}
              content={props.items.pages[4].content}
            />
        </div>
    )
}
