import React, { Component } from "react";
import home from "../../images/hero-images/home.jpg";
import hsq from "../../images/hero-images/hsq.jpg";
import services from "../../images/hero-images/services.jpg";
import customerResources from "../../images/window-images/services-2.jpg";
import contactUs from "../../images/hero-images/contact-us.jpg";
import becomeADriver from "../../images/hero-images/become-a-driver.jpg";
import "../styles/Hero.css";

class Hero extends Component {
  getImage() {
    switch (this.props.image) {
      case "hsq":
        return hsq;
      case "services":
        return services;
      case "customer-resources":
        return customerResources;
      case "contact-us":
        return contactUs;
      case "become-a-driver":
        return becomeADriver;
      default:
        return home;
    }
  }

  render() {
    return (
      <div
        className="hero-container desktop-only"
        style={{ backgroundImage: `url("${this.getImage()}")` }}
      >
        <div className={this.props.image === "hsq" ? "hsq-title title" : "title"}>
          <h1>{this.props.title}</h1>
          <h4>{this.props.subtitle}</h4>
        </div>
      </div>
    );
  }
}

export default Hero;
