import React, { Component } from "react";
import customerResources1 from "../../images/window-images/customer-resources-1.jpg";
import "../styles/TextWindow.css";

class SmallTextLeft extends Component {
  render() {
    return (
      <div
        className="image-container desktop-only"
        style={{ backgroundImage: `url("${customerResources1}")` }}
      >
        <div className="image-overlay">
          <div className="small-text-left-container">
            <div className="small-text-left-title window-title">
              <h2>{this.props.title}</h2>
            </div>
            <div className="small-text-left-content window-content">{this.props.content}</div>
          </div>
        </div>
      </div>
    );
  }
}

export default SmallTextLeft;
