import React, { Component } from "react";
import becomeadriver1 from "../../images/window-images/become-a-driver-1.jpg";
import home2 from "../../images/window-images/home-2.jpg";
import hsq1 from "../../images/window-images/hsq-1.jpg";
import hsq2 from "../../images/window-images/hsq-2.jpg";
import hsq3 from "../../images/window-images/hsq-3.jpg";
import services1 from "../../images/window-images/services-1.jpg";
import services2 from "../../images/window-images/services-2.jpg";
import services3 from "../../images/window-images/services-3.jpg";
import services4 from "../../images/window-images/services-4.jpg";
import "../styles/TextWindow.css";

class TextRight extends Component {
  getImage() {
    switch (this.props.image) {
      case "home-2":
        return home2;
      case "hsq-1":
        return hsq1;
      case "hsq-2":
        return hsq2;
      case "hsq-3":
        return hsq3;
      case "services-1":
        return services1;
      case "services-2":
        return services2;
      case "services-3":
        return services3;
      case "services-4":
        return services4;
      case "become-a-driver-1":
        return becomeadriver1;
      default:
        return home2;
    }
  }

  render() {
    return (
      <div>
        <div className="desktop-only">
          <div
            className="image-container"
            style={{ backgroundImage: `url("${this.getImage()}")` }}
          >
            <div className="image-overlay">
              <div className="text-right-container">
                {this.props.title !== undefined ? (
                  <div className="text-right-title window-title">
                    <h2>{this.props.title}</h2>
                  </div>
                ) : (
                  <div className="no-title"></div>
                )}
                <div className="text-right-content window-content">{this.props.content}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TextRight;
