import React from "react";
import { Link } from "react-router-dom";
import "../styles/Navbar.css";


function Navbar(props) {
  return (
    <div className="navbar-container desktop-nav" id="navbar">
      <div className="nav-items-container">
        <div
          className={props.active === "hsq" ? "nav-item active" : "nav-item"}
        >
          <Link to="/hsq">Healthcare Specialists and Qualifications</Link>
        </div>
        <div
          className={
            props.active === "services" ? "nav-item active" : "nav-item"
          }
        >
          <Link to="/services">Services</Link>
        </div>
        <div
          className={
            props.active === "customer-resources"
              ? "nav-item active"
              : "nav-item"
          }
        >
          <Link to="/customer-resources">Customer Resources</Link>
        </div>
        <div
          className={
            props.active === "contact-us" ? "nav-item active" : "nav-item"
          }
        >
          <Link to="/contact-us">Contact Us</Link>
        </div>
        <div
          className={
            props.active === "become-a-driver" ? "nav-item active" : "nav-item"
          }
        >
          <Link to="/become-a-driver">Become a Driver</Link>
        </div>
        <div className="nav-item">
          <a
            rel="noopener noreferrer"
            target="_blank"
            href="http://healthexcourier.com/employee-resources-login.php"
          >
            Employee Login
          </a>
        </div>
      </div>
    </div>
  );
}

export default Navbar;