import React from 'react'
import "../App.css";
import Hero from "../components/desktop/Hero";
import FeedbackForm from "../components/desktop/FeedbackForm";
import SmallTextLeft from "../components/desktop/SmallTextLeft";
import InsuranceCertForm from "../components/desktop/InsuranceCertForm";
import MobilePage from "../components/mobile/MobilePage";
import MobileFeedback from '../components/mobile/MobileFeedback';
import MobileInsurance from '../components/mobile/MobileInsurance';
import SubmitSuccess from '../components/SubmitSuccess';
import SubmitError from '../components/SubmitError';

export default function CustomerResources(props) {
    return (
        <>
            {/* Mobile Components */}
            <SubmitSuccess />
            <SubmitError />
            <MobileFeedback 
              hero={true}
              image="customer-resources-hero"
            />
            <MobilePage 
              image="customer-resources-1"
              title={props.items.textLefts[0].title}
              content={props.items.textLefts[0].content}
              />
            <MobileInsurance />

            {/* Desktop Components */}

            <Hero
              title={props.items.hero.title}
              subtitle={props.items.hero.subtitle}
              image={props.items.switch}
            />
            <FeedbackForm />
            <SmallTextLeft
              title={props.items.textLefts[0].title}
              content={props.items.textLefts[0].content}
            />
            <InsuranceCertForm />
        </>
    )
}
