import React from "react";
import { Link } from "react-router-dom";
import logo from "../images/logo.png";
import "./styles/NavbarLogo.css";

function Logo() {
  return (
    <div className="logo-container">
      <div className="logo">
        <Link to="/">
          <img src={logo} alt="HealthEx" />
        </Link>
      </div>
      <h4>
        For Immediate Service, <a href="tel:5163499889">Call: (516) 349 9889</a> ~ <a href="tel:8553499889">Toll Free: (855) 349 9889</a>
      </h4>
    </div>
  );
}

export default Logo;
